.container {
  max-width: 700px;
  margin: auto;
  
  font-family: Arial, sans-serif;
}

.button-group {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 15px;
}

.button,
.active-button {
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.button {
  background-color: #f0f0f0;
  color: black;
}

.active-button {
  background-color: #4caf50;
  color: white;
  border: none;
}

textarea {
  width: 90%;
  padding: 4px;
  font-size: 16px;
  border-radius: 5px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
}

.submit-button {
  padding: 10px 20px;
  font-size: 14px;
  background-color: #80EA6E;
  color: black;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  margin-bottom: 10px;
}

.output-box {
  background: #f7f7f7;
 
  border-radius: 5px;
  white-space: pre-wrap;
   margin-top: 20px;
  text-align: left;
  background-color: #f4f4f4;
  
  border-radius: 5px;
  margin-bottom: 40px;
}
