
.App {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  padding: 10px;
  
  transition: width 0.4s ease;
  overflow-y: auto;
  height: 100vh;
}


.spring-image{
  width: 20px;
  height: 20px;
}
.headline{
  padding-top: 11px;
  padding-bottom: 11px;
  font-weight: bold;
  background-color: #6DB33F;
}
.tabs {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  background-color: #f0f0f0;
  font-weight: bold;
}

button.active {
  background-color: #80EA6E;
  color: #000;
}

input {
  padding: 10px;
  width: calc(100% - 22px);
  margin-bottom: 10px;
  box-sizing: border-box;
}

.chat-input{
   width: 330px;
}

.start-input{
  width: 330px;
}

.tab-content {
  margin-top: 20px;
}

.hidden-tab {
  display: none;
}




.output {
  margin-top: 20px;
  text-align: left;
  background-color: #f4f4f4;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 40px;
}

.all_output{
  background-color: #80EA6E;
  color: black;
}


.chat-text,
.recipe-text,
.startup-text,
.history-text {
  width: 100%;
  padding: 16px;
  box-sizing: border-box;
  white-space: pre-wrap; /* Allows wrapping for newlines and long lines */
  word-break: break-word; /* Breaks long words to fit within container */
  font-size: 16px;
  line-height: 1.6;
  border-radius: 10px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
  overflow: visible;     /* Don't cut off content */
  background-color: #f4f4f4;
  color: #000;
}



.layout {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

/* Sidebar styles */
.sidebar {
  width: 260px;
  background-color: #1e1e1e;
  color: white;
  padding: 20px;
  overflow-y: auto;
}

/* Main content panel */

/* Toggle Button */
.sidebar-toggle {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 999;
  padding: 8px 14px;
  background-color: #1ab732;
  color: black;
  border: none;
  font-size: 14px;
  border-radius: 6px;
  cursor: pointer;
}

.sidebar-toggle:hover {
  background-color: #105428;
}

.loading-para{
  text-align: center;
  color:blue;
}

.region-label {
  font-weight: bold;
  font-size: 16px;
  display: block;
  margin-top: 20px;
  margin-bottom: 8px;
  color: #333;
  padding-right: 30px;
}

.region-select {
  width: 50%;
  padding: 8px;
  font-size: 15px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  outline: none;
  transition: 0.3s;
}

.region-select:hover {
  border-color: #4CAF50;
  background-color: #fff;
}

.region-select:focus {
  border-color: #00ff04;
}
