
.sidebar {
  width: 190px;
  background-color: #CECECE;
  color: #f0f0f0;
  padding: 20px;
  height: 100vh;
  overflow-y: auto;
  position: fixed;
  left: 0;
  top: 0;
  
  
}

.sidebar h2  {
  text-align: center;
  margin-bottom: 10px;
  color: #1d9e2a;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar li {
  padding: 10px;
  margin-bottom: 10px;
  background-color: #2c2c2c;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.sidebar li:hover {
  background-color: #3a3a3a;
}

.timestamp {
  font-size: 0.8em;
  color: #aaa;
}

.empty {
  font-style: italic;
  color: #888;
}

.delete-btn {
  margin-top: 5px;
  background-color: #ff4d4d;
  border: none;
  padding: 5px 8px;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}
